@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 125%; }

li {
  list-style-type: none; }

.wrapper {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto; }

body {
  background-color: #0066cc;
  background-image: url(data:image/png;base64,);
  color: #10130e;
  text-align: center; }

input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
  margin: 0; }

h1,
h2,
h3,
h4,
button,
.fa-minus-circle {
  margin: 10px 0;
  font-family: "Righteous", cursive;
  text-transform: uppercase; }

h1 {
  color: #A9C1D9;
  font-size: 3rem; }
  @media (min-width: 900px) {
    h1 {
      font-size: 4rem; } }

header button {
  border-radius: 5px;
  border: none;
  background-color: #A9C1D9;
  display: block;
  margin: 10px auto; }
  header button:hover, header button:focus {
    box-shadow: 0 0 3px 2px #A9C1D9; }

main {
  min-height: 85vh; }
  main h3 {
    background-color: #F2F0DF;
    border-radius: 5px;
    display: inline-block;
    padding: 5px 10px; }
    @media (min-width: 900px) {
      main h3 {
        align-self: center; } }
  main label,
  main input {
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    margin: 20px 0; }
    main label:hover, main label:focus,
    main input:hover,
    main input:focus {
      box-shadow: 0 0 3px 2px #A9C1D9; }
  @media (min-width: 900px) {
    main input {
      width: calc(50% - 10px);
      display: inline-block;
      margin-right: 10px; } }
  @media (min-width: 900px) {
    main form {
      text-align: left; }
      main form div {
        display: flex;
        justify-content: center; }
        main form div button {
          margin: 10px 5px; } }
  main select {
    width: 100%;
    margin: 10px 0;
    height: 1.6rem;
    border-radius: 5px; }
    main select:hover, main select:focus {
      box-shadow: 0 0 3px 2px #A9C1D9; }
    @media (min-width: 900px) {
      main select {
        width: calc(50% - 10px);
        display: inline-block;
        margin-right: 10px; } }
  main button {
    border-radius: 5px;
    border: none;
    background-color: #A9C1D9;
    display: block;
    margin: 10px auto; }
    main button:hover, main button:focus {
      box-shadow: 0 0 3px 2px #A9C1D9; }
  main .fa-minus-circle {
    cursor: pointer;
    border-radius: 5px;
    font-family: FontAwesome !important; }
    main .fa-minus-circle:hover, main .fa-minus-circle:focus {
      color: #FF0000; }
  main .flexParent {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; }
    @media (min-width: 900px) {
      main .flexParent {
        flex-direction: row; } }
    main .flexParent div h2,
    main .flexParent div ul {
      background-color: #F2F0DF;
      border-radius: 5px; }
      main .flexParent div h2 li,
      main .flexParent div ul li {
        text-transform: capitalize; }
    @media (min-width: 900px) {
      main .flexParent div {
        width: calc(50% - 10px);
        margin-right: 10px; } }
  main .colorPop {
    color: white; }
  main .colorPositive {
    color: green; }
  main .colorNegative {
    color: red; }

canvas {
  background-color: #FFFFFF;
  margin: 20px 0; }

footer {
  height: 5vh;
  color: #A9C1D9;
  font-family: "Righteous", cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold; }

